// item-slider styles
.m-item-slider{
  position: relative;
  z-index: 0;
  margin: auto 0;
  &__item{
      height:100%;
      width: 100%;
      // max-width: 1920px;
      align-items: center;
    }
    &__img{
      display: inline-flex;
      height:55vh;
      // margin-top: 100px;
      width: 100%;
      @include backgroundCoverCenter;

      z-index: 1;
  }
  &__content{
      @include absolutelyFull;
      display: inline-flex;
      align-items: center;
  }
}