// intro-slider styles
.m-intro-slider{
    position: relative;
    z-index: 0;
    &__item{
        height:100%;
        width: 100%;
        max-width: 1920px;
        display: inline-flex;
        align-items: center;
    }
    &__img{
        height: calc(100vh - 75px);
        margin-top: 75px;
        width: 100%;
        @include backgroundCoverCenter;
        z-index: 1;
    }
    &__content{
        @include absolutelyFull;
        display: inline-flex;
        align-items: center;
    
    }
    &__title{
        position: relative;
        z-index: 2;
        display: inline-flex;
        flex-direction: column;
        width: 100%;
        text-align: center;
        text-transform: uppercase;
        font-size: $fs-36;
        color: $c-grey;
        letter-spacing: 1.8px;
        padding: 35px 0;
        background-color: rgba($c-black, $alpha: .55);
        & span{
            font-family: $f-OpenSans-b;
        }
    }
}