// section-footer styles
.o-section-footer{
  &__container{
    display: inline-flex;
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__contact{
    position: absolute;
    top: 35vh;
    right: 47%;
    align-self: center;
  }
  &__map{
    height: inherit;
    padding: 0;
    border: 0;
  }
}