// item-review styles
.m-item-review{
  position: relative;
  z-index: 0;
  margin: auto 0;
  &__item{
      height:100%;
      width: 100%;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

    }
    &__img{
      display: inline-flex;
      height:225px;
      // margin-top: 100px;
      width: 225px;
      border-radius: 100%;
      @include backgroundCoverCenter;

      z-index: 1;
  }
  &__text{
    width: 75%;
    padding-top: 7%;
    font-size: $fs-20;
    color: $c-violet;
    text-align: justify;

  }
}