// section-reviews styles
.o-section-reviews{
  &__container{
    height: 100%;
    padding: 10vh 0 0;
    
    // margin: 0 auto;
  }
  &__title{
    display: inline-flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    text-transform: uppercase;
    font-size: $fs-30;
    letter-spacing: 1.5px;
    color: $c-violet;
    font-weight: $fw-bold;
  }
  &__items{
    font-weight: $fw-bold;    
    position: relative;
    margin: 15vh 0;
  }
}