// header styles
.o-header{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    background-color: $c-white-3;
    border: solid 1px $c-violet;
    height: 75px;
    width: 100%;
    display: inline-flex;
    align-items: center;
    font-weight: $fw-bold;
    font-family: $f-OpenSans-b;
    
    &__container{
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        padding: 0;
    }
    &__item{
        display: inline-flex;
        
        align-items: center;
    }
    &__phones{
        padding-left: 30px;
        display: inline-flex;
        flex-direction: column;
        font-style: 16px;
        line-height: $lh-1-7;
    }
    &__menu{
        font-size: $fs-14;
    }
    &__button{
        margin: 0 12px;
    }
}