// item-advantage styles
.m-item-advantage{
    
    &__items{
        display: inline-flex;
        flex-wrap: wrap;
        // align-items: center;
        // justify-content: space-between;
        height: 75vh;
        padding-top: 5vh;
        
    }
    &__item{
        padding-top: 5vh;
        padding-bottom: 5vh;
        
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        // padding-top: 50px;
        // padding-top: 6vh;
    }
    &__subtitle{
        padding-top: 4vh;
        font-size: $fs-18;
        line-height: $lh-1-3;
        text-align: center;
        color: $c-white-10;
        width: 75%;
        letter-spacing: 1.1px;
    }
    &__img{
        height: 10vh;
        width: auto;
    }
}