// menu styles
.menu {
  margin-top: 22px;
  
  &__item {
    color: $c-violet;
    display: inline-flex;
    // background: $c-dark;
    // color: $c-white;
    padding: 5px 12px;
    position: relative;
    &:after{
      position: absolute;
      margin: 0 auto;
      content: '';
      bottom: 4px;
      width: 0;
      left:  0;
      right: 0;
      height: 2px;
      background-color: $c-violet;
      transition: width $t-0-35;
      border-radius: 3px;
    }
    &:hover, &--active {
      &:after{
        width: calc(100% - 24px);
      }
    }
  }
}