.a-checkbox {
  user-select: none;
}

.a-checkbox__input {
  position: absolute;
  opacity: 0;
}

.a-checkbox__input:hover ~ .a-checkbox__label:before {
  background: #f35429;
}

.a-checkbox__input:focus ~ .a-checkbox__label:before {
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
}

.a-checkbox__input:checked ~ .a-checkbox__label:before {
  background: #f35429;
}

.a-checkbox__input:checked ~ .a-checkbox__label:after {
  content: '';
  position: absolute;
  left: 5px;
  top: 9px;
  background: white;
  width: 2px;
  height: 2px;
  box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
  transform: rotate(45deg);
}

.a-checkbox__input:disabled ~ .a-checkbox__label:before {
  box-shadow: none;
  background: #ddd;
}

.a-checkbox__input:disabled ~ .a-checkbox__label {
  color: #b8b8b8;
  cursor: default;
}

.a-checkbox__label {
  position: relative;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  padding: 0;
}

.a-checkbox__label:before {
  content: '';
  margin-right: 10px;
  display: inline-block;
  vertical-align: text-top;
  width: 20px;
  height: 20px;
  background: white;
}

