// social styles
.m-social{
    &__link{
        height: 60px;
        width: 60px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border: 3px solid $c-violet;
        border-radius: 100%;
        transition: background-color $t-0-35;
        margin-right: 25px;
        &:hover{
            background-color:rgba($c-violet-2, $alpha: .5) ;
        }
    }
    &__img{
        height: 30px;
        width: auto;
    }
}