// contact styles
.m-contact{
    border-radius: 25px;
    background-color: $c-white-3;
    border: 1px solid $c-violet;    
    max-width: 420px;
    padding: 5vh 40px 2vh;
    @include media('<large') {
        max-width: 350px;
        padding: 5vh 30px 2vh;
    }
    &__content{
        display: inline-flex;
        flex-direction: column;
    }
    &__address, &__phone{
        font-family: $f-OpenSans;
        font-size: $fs-24;
        font-weight: $fw-bold;
        letter-spacing: 1.2px;
        text-align: center;
        color: $c-violet;
        padding-bottom: 5px;
        @include media('<large') {
            font-size: $fs-20;
       
        }
    }
    &__address{
        padding-bottom: 15px;
    }
    &__social{
        align-self: center;
        padding-top: 30px;
    }
}