// form styles
.m-form{
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  &__thanks{
    color: $c-grey;
    font-weight: $fw-bold;
    text-align: center;
    font-size: $fs-24;
    display: none;
    align-items: center;
    height: 75%;
    transition: display $t-0-5;
    &--visible{
      display: inline-flex;
    }
  }
  & label{
    color: $c-grey;
    transform: translateY(-50%);
    font-weight: $fw-extrabold;
    height: 2vh;
    display: block !important;
    color: lightcoral;
  }
  &__title{
    text-transform: uppercase;
    letter-spacing: 1.5px;
    font-size: $fs-30;
    color: $c-grey;
    font-weight: $fw-bold;
    width: 70%;
    padding-top: 4vh;
  }
  &__content{
    display: inline-flex;
    flex-direction: column;
    max-width: 410px;
    // width: 100%;
    padding-top: 12vh;
  }
  &__field{
    margin-bottom: 2vh;
    &__label-captcha{
      width: 50%;
    }
  }
  &__captcha{
    display: flex;
    // max-width: 380px;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 25px;
    cursor: pointer;
    width: 100%;
    &-img{
      width: 40%;
      height: 50px;
    }
  }
  &__button{
    width: 160px;
    height: 65px;
    text-transform: uppercase;
    font-size: $fs-18;
    color: $c-grey;
    border: 2px solid $c-grey;
    background: none;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    &:after {
      background: $c-white;
      content: "";
      height: 110px;
      left: -75px;
      opacity: .2;
      position: absolute;
      top: -30px;
      width: 25px;
      transform: rotate(35deg);
      z-index: 1;
      transition: left 1s cubic-bezier(0.19, 1, 0.22, 1);
    }
    &:focus {
      border:0;
      outline: 0;
    }
    &:hover{
      cursor: pointer;
      &:after{
        left: 120%;
        
      }
    }
  }
}